body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background: black;
}

.header {
  text-align: center;
  height: 2rem;
  line-height: 2rem;
  background: yellow;
}

.searchbox {
  text-align: center;
  margin: auto;
  height: 10rem;
  background: white;
  margin-top: 4rem;
}

.searchbox--input {
  width: 30%;
  min-width: 300px;
  height: 4rem;
  line-height: 4rem;
  font-size: 2rem;
  border-radius: 10px;
}

.searchbox--output {
  width: 30%;
  min-width: 300px;
  height: 4rem;
  line-height: 4rem;
  font-size: 2rem;
  border-radius: 10px;
}

.searchbox--input::-webkit-input-placeholder {
  text-indent: 5px;
}

.searchbox--input:-ms-input-placeholder {
  text-indent: 5px;
}

.searchbox--input::placeholder {
  text-indent: 5px;
}

.searchbox .suggestion-container {
  position: relative;
  margin: auto;
  width: 20%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 10px;
}

.searchbox .suggestion-container .suggestion{
  flex: 1 0 100%;
  background: white;
  color: black;
  margin: 1px;
  border: 2px solid black;
  font-size: 1rem;
  border-radius: 5px;
  height: auto;
  padding: 5px;
  text-align: left;
}

.searchbox .suggestion-container .suggestion:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}


